*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

#container{
  background-color: #F7DDD4;
  height: 100vh;
  display: flex; 
}

#pomodoro-clock{
  margin: auto;
  border: 2px solid black;
  height: 460px;
  width: 300px;
  background-color: #448D76;
  color: white;
  text-align: center;
  border-radius: 20px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

#settings-container{
  display: flex;
  text-align: center;
  margin: 10px 5px;
}

#session-label, #break-label{
  width: 50%;
  height: auto;
}
#left-side, #right-side{
  display: flex;
  justify-content: space-evenly;
}

.settings-button{
  display: block;
  width: 50px;
  height: 50px;
  margin: 10px;
  background-color: gray;
  border-radius: 10px;
}

#session-length, #break-length {
  font-size: 40px;
  margin: auto;
  width: 60px;
  background-color: black;
  border-radius: 10px;
}

.set-title{
  font-size: 20px;
}

#timer-label{
  width: 260px;
  background-color: black;
  margin: auto;
  padding: 10px;
  border-radius: 10px;
}

#time-left{
  font-size: 50px;
  font-weight: bolder;
}

#timer{
  font-size: 20px;
}

#title{
  font-size: 35px;
  font-weight: bolder;
}

#start_stop, #reset {
  background-color: black;
  border-radius: 10px;
  margin: 5px 10px;
  padding: 5px;
  width: 120px;
  font-size: 18px;
  font-weight: bolder;
}

#reset{
  color: red;
}

#start_stop{
  color: green;
}